@import '../../shared/styles/media_mixin';

.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 240px;
  justify-content: space-between;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  align-items: center;

  @include _768 {
    width: auto;
    gap: 150px;
  }
}

.button {
  display: block;
  width: 10px;
  height: 10px;

  position: relative;

  background-color: var(--primary-blue-color);
  border-radius: 50%;
  border: none;

  cursor: pointer;
  -webkit-transition: background-color 1s;
  -o-transition: background-color 1s;
  transition: background-color 1s;

  &::after {
    content: attr(data-content);
    display: block;
    left: 50%;
    position: absolute;
    min-width: 70px;
    top: -80%;
    transform: translate(-50%, -50%);
    color: var(--primary-blue-color);
    font-family: Spartan;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    text-transform: capitalize;
    text-align: center;
  }

  &:not(:first-child)::before {
    content: '';
    display: block;

    width: 23px;
    height: 1px;
    right: 100%;

    position: absolute;
    top: 50%;
    transform: translate(0, -50%);

    background-color: inherit;

    cursor: auto;
  }

  &[disabled] {
    cursor: auto;
    background-color: var(--light-gray-color);
  }
  &[disabled]::after {
    color: var(--light-gray-color);
  }

  @include _768 {
    width: 16px;
    height: 16px;

    &:not(:first-child)::before {
      width: 150px;
    }
  }
}
