.btnItem {
  width: 90%;
  border: none;
  background-color: transparent;
  padding: 10px;
  text-align: start;
  margin: 10px auto;
  box-sizing: border-box;
  border: 1px solid var(--main-color);
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  &:hover {
    background-color: var(--primary-blue-color);
    color: var(--contrasting-color);
    border: 1px solid var(--primary-blue-color);
  }
}

.item {
  list-style: none;
}

.email {
  margin-top: 7px;
}

.listItem {
  height: 235px;
  overflow-y: auto;
  margin: 0;
  padding-left: 0;

  &::-webkit-scrollbar {
    position: absolute;
    z-index: 12;
    width: 0.7em;
    border-radius: 7px;
    background: linear-gradient(
      90deg,
      rgba(204, 204, 204) 0%,
      rgba(204, 204, 204, 0) 50%,
      rgb(204, 204, 204) 100%
    );
  }

  &::-webkit-scrollbar-thumb {
    background: var(--primary-blue-color);
    border-radius: 7px;
  }
}

.modalWrapper {
  z-index: 1;
  position: absolute;
  visibility: hidden;
  left: 0;
  top: 80px;
  height: 0;
  overflow: hidden;
  border-radius: 10px;
  border: 0.5px solid var(--text-color);
  background-color: var(--contrasting-color);
  transition: all 0.5s ease-in-out;
}

.open {
  visibility: visible;
  height: 235px;
}
