.hint {
  padding: 16px 30px;
  border-radius: 17px;

  backdrop-filter: blur(8.25px);
  position: relative;
  margin-top: auto;
  max-width: 475px;
  width: 100%;
  box-sizing: border-box;
}

.hintText {
  text-align: center;
  font-family: Spartan;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

