.button {
  width: 97px;
  height: 30px;

  position: relative;

  background-color: transparent;
  border: none;

  cursor: pointer;

  & > svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    & * {
      transition: fill var(--duration);
    }
  }

  & > span {
    width: 80px;
    height: 19px;

    font-size: 16px;
    font-family: TT Norms;
    font-weight: 500;
    text-transform: uppercase;
    color: #fff;
    text-align: center;

    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);

    user-select: none;
  }

  &:hover > svg * {
    fill: var(--primary-hovered);
  }

  &:disabled {
    cursor: auto;
  }

  &:disabled > svg * {
    fill: var(--light-gray-color);
  }

  &:global(.type-next) > span {
    left: 0;
  }

  &:global(.go-to-po) > span {
    left: 0;
    width: 110px;
  }
  &:global(.go-to-po) > svg * {
    width: 100px;
  }
  &:global(.go-to-po) > svg {
    width: 140px;
  }
  &:global(.go-to-po) {
    width: 140px;
  }
}
