.label {
  font-family: var(--main-font);
  font-size: 20px;
  text-transform: capitalize;
  margin-top: 20px;
  height: 23px;
  cursor: pointer;
  user-select: none;
  display: flex;
}

.text {
  margin-top: 5px;
}

.checkbox {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;

  &:checked + .customize::before {
    display: block;
  }
}

.customize {
  display: inline-block;
  width: 23px;
  height: 23px;
  border-radius: 3px;
  margin-right: 14px;
  border: 1px solid black;

  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;

    background-image: url(../../../images/svg/check.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    transform: translate(-50%, -50%);
    display: none;
  }
}

.btnGrop {
  display: flex;
  margin-top: 30px;
  align-items: flex-end;
  justify-content: space-between;
}

.deleteBtn,
.printBtn {
  border: none;
  cursor: pointer;
  background-color: transparent;
  width: 33px;
  height: 33px;
  padding: 0;
}

.deleteBtn {
  margin-right: 21px;
}

.submitBtn {
  padding: 5px 21px;
  color: var(--contrasting-color);
  margin-right: 10px;
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  background-color: var(--primary-blue-color);
  border-radius: 7px;
  border: none;
  cursor: pointer;
}

.updateBtn {
  padding: 5px 10px;
  color: var(--light-gray-color);
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 7px;
  background-color: var(--light-blue-color);
  border: none;
  cursor: pointer;
}

.thirdGroup {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.wrongPrice {
  color: var(--accent-red-color);
}

.notActiveSubmitBtn {
  color: var(--light-gray-color);
}

.activeUpdateBtn {
  color: var(--accent-red-color);
}

.totalPrice {
  text-transform: uppercase;
  font-family: var(--secondary-font);
  font-size: 16px;
}

.valueTotalPrice {
  margin-bottom: 14px;
  text-align: right;
  font-family: var(--secondary-font);
  font-size: 24px;
  text-transform: uppercase;
  margin-left: 20px;
}

.dealerPrice {
  text-align: right;
  font-family: Spartan;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: capitalize;
  margin-top: 14px;
}

.valueDealerPrice {
  margin-left: 10px;
  text-align: right;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.wrapper {
  margin-right: 20px;
  margin-top: auto;
}
