.modal {
  position: absolute;
  top: -40px;
  background-color: var(--contrasting-color);
  border-radius: 10px;
  width: 475px;
  height: 395px;
  box-shadow: 4px 4px 14.7px 0px #00000040;
  backdrop-filter: blur(18px);
  overflow-y: auto;
  box-sizing: border-box;
  padding-left: 12px;
  padding-right: 12px;

  &::-webkit-scrollbar {
    position: absolute;
    z-index: 12;
    width: 0.7em;
    border-radius: 7px;
    background: linear-gradient(
      90deg,
      rgba(204, 204, 204) 0%,
      rgba(204, 204, 204, 0) 50%,
      rgb(204, 204, 204) 100%
    );
  }

  &::-webkit-scrollbar-thumb {
    background: var(--primary-blue-color);
    border-radius: 7px;
  }
}

.title {
  font-size: 20px;
  font-family: TT Norms;
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
  margin-top: 15px;
  margin-bottom: 11px;
}

.windowFilterBtn {
  width: 140px;
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  padding-top: 8px;
  padding-bottom: 5px;
  font-size: 14px;
  font-weight: 400;
  font-family: Spartan, sans-serif;
  text-transform: uppercase;
  background-color: var(--contrasting-color);
  cursor: pointer;
  text-align: center;
  display: block;
  &:hover {
    background-color: var(--light-blue-color);
    border: 0.5px solid var(--light-blue-color);
    color: var(--primary-blue-color);
  }
}

.filterDoorBtnList {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: auto;
  gap: 12px 34px;
  justify-content: center;
  align-items: center;
}

.doorFilterBtn {
  font-family: var(--main-font);
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  cursor: pointer;
  background-color: var(--contrasting-color);
  box-sizing: content-box;

  &:nth-child(6n-5),
  &:nth-child(6n-4) {
    width: 178px;
    padding-top: 8px;
    font-size: 14px;
    padding-bottom: 5px;
  }

  &:nth-child(6n-3),
  &:nth-child(6n-2) {
    width: 94px;
    padding-top: 6px;
    padding-bottom: 4px;
    font-size: 12px;
  }

  &:nth-child(6n-1),
  &:nth-child(6n) {
    width: 111px;
    padding-top: 6px;
    padding-bottom: 4px;
    font-size: 12px;
  }

  &:hover {
    background-color: var(--light-blue-color);
    border: 0.5px solid var(--light-blue-color);
    color: var(--primary-blue-color);
  }

  &:nth-child(2n + 1) {
    margin-left: auto;
  }
}

.outsideSvg,
.insideSvg {
  width: 135px;
  height: 53px;
}

.btnSubtitle {
  margin-top: 16px;
  text-align: center;
  font-size: 12px;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.insideText,
.outsideText {
  font-size: 10px;
  text-align: center;
  font-family: var(--main-font);
}

.doorsIcon {
  width: 178px;
  height: 143px;
  border: 0.5px solid var(--main-color);
  border-radius: 16px;
  background-color: transparent;
  cursor: pointer;
  margin-left: 30px;
}

.doorsImage {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-left: 50px;
}

.filterWindowBtnList {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: auto;
  gap: 13px 9px;
  justify-content: center;
  align-items: center;
}

.choiceBtn,
.windowBtnDouble {
  margin-top: 30px;
  border: none;
  display: flex;
  align-items: center;
}

.windowsImage,
.windowsImageDouble {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-left: 40px;
}

.windowsIcon {
  width: 177px;
  height: 213px;
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  cursor: pointer;
  background-color: transparent;
  margin-left: 30px;
}

.windowsIconDouble {
  width: 178px;
  height: 160px;
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  cursor: pointer;
  background-color: transparent;
  margin-left: 30px;
}

.windowsIcon img {
  margin-top: 18px;
  width: 113px;
  height: 158px;
}

.imgWrapper {
  display: flex;
  justify-content: center;
  margin-top: 17px;
}

.imgWrapper img {
  width: 73.5px;
  height: 103px;
}

.btnSubtitle {
  margin-top: 14px;
  text-align: center;
  font-size: 12px;
  font-weight: 400;
  font-family: Spartan, sans-serif;
  text-transform: capitalize;
}

.tAtIconReverse,
.swingIconReverse {
  transform: rotateY(180deg);
}

.french,
.frenchPng {
  width: 178px;
  height: 176px;
}

.singlePng,
.frenchPng {
  display: block;
}

.singleGif,
.singlePng {
  width: 180px;
  height: 210px;
}

.singleGif {
  display: none;
}

.french {
  display: none;
}

.windowsImage:hover .singleGif {
  display: block;
}

.windowsImage:hover .singlePng {
  display: none;
}

.windowsImage:hover .french {
  display: block;
}

.windowsImage:hover .frenchPng {
  display: none;
}

.singleDoorGif,
.singleDoorPng {
  width: 120px;
  height: 180px;
}

.singleDoorPng,
.doubleDoorPng {
  display: block;
}

.singleDoorGif {
  display: none;
}

.doubleDoorGif {
  display: none;
}

.doorsImage:hover .singleDoorGif {
  display: block;
}

.doorsImage:hover .singleDoorPng {
  display: none;
}

.doorsImage:hover .doubleDoorGif {
  display: block;
}

.doorsImage:hover .doubleDoorPng {
  display: none;
}

.doubleDoorGif,
.doubleDoorPng {
  width: 170px;
  height: 180px;
}
