.modalWrapper {
  width: 312px;
  border: 3px solid var(--accent-red-color);
  border-radius: 10px;
  background-color: var(--contrasting-color);
  padding: 22px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text {
  width: 200px;
  font-family: var(--main-font);
  text-align: center;
  margin-bottom: 18px;
}

.btn {
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 500;
  width: 178px;
  padding: 10px 0;
  display: block;
  text-transform: uppercase;
  border: none;
  border-radius: 9px;
  color: var(--contrasting-color);
  background-color: var(--primary-blue-color);

  &:first-of-type {
    margin-bottom: 9px;
  }
}
