.form {
  display: flex;
  justify-content: center;

  font-family: TT Norms;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.inputWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.text {
  color: var(--main-color);
  text-align: center;
  font-size: 14px;
  font-family: TT Norms;
  margin-top: 52px;
  margin-left: 10px;
}

.inputSize::-webkit-outer-spin-button,
.inputSize::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.inputSize {
  margin-top: 18px;
  width: 110px;
  height: 52px;
  border-radius: 4px;
  border: 1px solid #adadad;
  color: var(--main-color);
  text-align: center;
  font-size: 18px;
  font-family: TT Norms;
  margin-bottom: 14px;
}

.label {
  color: var(--main-color);
  text-align: center;
  font-size: 14px;
  text-transform: uppercase;
}

.multiplyIcon {
  width: 11px;
  height: 11px;
  margin: 58px 13px 0 13px;
}

.hint {
  position: absolute;
  font-size: 14px;
  color: #f5a403;
  bottom: -20px;
  font-family: TT Norms;
}

.borderSize {
  font-family: TT Norms;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  font-size: 12px;
  text-align: center;
}

.btn {
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 29px;
  text-transform: uppercase;
  border-radius: 7px;
  width: 100%;
  max-width: 272px;
  height: 38px;
  font-family: TT Norms;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  font-size: 16px;
  text-align: center;
  cursor: pointer;
  border: none;
  background-color: var(--primary-blue-color);
  &:hover {
    background-color: var(--primary-hovered);
  }
  color: white;
}
