.indoOrderWrappper {
  display: flex;
  flex-direction: row;
  margin-top: 25px;
  margin-bottom: 14px;
}

.firstWrapper,
.secondWrapper {
  margin-right: 50px;
}

.unitList {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.title {
  margin-bottom: 9px;
  font-family: var(--main-font);
  font-weight: 500;
  text-transform: capitalize;
}

.info {
  margin-left: 10px;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.input {
  width: 184px;
  border-radius: 7px;
  border: 0.5px solid var(--main-color);
  color: var(--text-color);
  margin-left: 6px;
  font-family: var(--main-font);
  text-transform: lowercase;
  padding: 5px;
}

.label {
  color: var(--text-color);
  font-family: var(--main-font);
  font-size: 20px;
  text-transform: capitalize;
  display: flex;
  align-items: center;
}
