.label {
  display: flex;
  align-items: center;
  gap: 26px;

  font-family: TT Norms;
  font-size: 14px;
  cursor: pointer;
  user-select: none;

  &:global(.reversed) {
    flex-direction: row-reverse;
  }

  &:global(.column) {
    flex-direction: column-reverse;
    gap: 14px;
    width: fit-content;
  }
}

.checkbox {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;

  &:checked + .customize::before {
    display: block;
  }
}

.customize {
  display: inline-block;
  width: 23px;
  height: 23px;
  border-radius: 3px;
  border: 1px solid var(--main-color);

  position: relative;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;

    background-image: url(../../../images/svg/cheked.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    transform: translate(-50%, -50%);
    display: none;
  }
}
