.button {
  width: 157px;
  height: 194px;

  padding: 0;

  background-color: #fff;

  border: 9px solid var(--light-gray-color);

  cursor: pointer;

  &:hover,
  &:focus {
    background-color: var(--interactive-color);
  }

  & > .divider {
    display: block;
    background-color: var(--gray-color);
    margin: 0 auto;
  }

  & > .divider:global(.vertical) {
    width: 9px;
    height: 100%;
  }

  & > .divider:global(.horizontal) {
    width: 100%;
    height: 9px;
  }

  &:disabled {
    &:hover {
      background-color: #fff;
    }

    cursor: auto;

    & > .divider {
      background-color: var(--light-gray-color);
    }
  }
}
