.modalWrapper {
  width: 191px;
  border-radius: 10px;
  background-color: var(--light-blue-color);
  display: flex;
  flex-direction: column;
  padding: 0;
  visibility: hidden;
  height: 0;
  position: absolute;
  transition: all 0.5s ease-in;
  left: 0;
  top: 40px;
  z-index: 100;
  box-sizing: border-box;
  overflow: hidden;
}

.label {
  color: var(--text-color);
  text-transform: capitalize;
  cursor: pointer;
  &:not(:last-child) {
    margin-bottom: 11px;
  }
}

.input {
  margin-right: 5px;
}

.open {
  height: 112px;
  visibility: visible;
  padding: 18px 0 18px 14px;
}

.selectBtn {
  border: none;
  background-color: transparent;
  display: flex;
  align-items: baseline;
  color: var(--text-color);
  font-size: 20px;
  text-transform: capitalize;
  padding: 0;
  cursor: pointer;
  font-family: var(--main-font);
}

.rotated {
  transform: rotate(180deg);
}

.arrow {
  margin-left: 5px;
  transition: transform 0.5s ease;
}
