.btnDoor {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;

  margin-bottom: 16px;

  &:hover {
    & path:first-child {
      fill: #005aaa;
      transition: fill 0.3s ease;
    }
  }
}

.btnWindow {
  width: 172px;
  height: 172px;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  position: relative;
  transition: background-color 0.3s ease;
  margin-bottom: 16px;

  &:hover {
    & rect {
      fill: #005aaa;
    }
  }
}

.svgFrameWindow {
  top: 17px;
  right: 84px;
  position: absolute;
  pointer-events: none;
}

.svgFrameDoor {
  top: 43px;
  right: 57px;
  position: absolute;
  pointer-events: none;
}

.wrapper {
  display: flex;
  justify-content: center;
  gap: 44px;
  position: relative;
}

.windowText {
  transform: translate(-50%, -50%);
  font-size: 20px;
  font-family: TT Norms;
  text-transform: uppercase;
}

.buttonWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  & p {
    max-width: 157px;

    font-size: 14px;
    font-family: TT Norms;
    text-transform: uppercase;
    text-align: center;
    line-height: normal;
  }
}
