.template {
  width: 100%;
  min-height: 100vh;
  margin-right: -200px;
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 80%;
  background-size: cover;
  background-image: url(../../images/background.png);

  @media screen and (min-width: 1024px) {
    background-position: 0;
  }
}

.container {
  width: 100%;
}

.wrapper {
  max-width: 340px;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;

  @media screen and (min-width: 1024px) {
    margin: 0 144px 0 auto;
  }
}

.logo {
  margin: auto;
}

.title {
  margin-top: 18px;
  color: var(--primary-blue-color);
  text-align: center;
  font-family: var(--main-color);
  font-size: 24px;
  font-weight: 600;
  text-transform: capitalize;
}

.divider {
  width: 100%;
  margin: 25px auto 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  color: var(--main-color);
  font-family: var(--main-font);
  text-align: center;

  &::before,
  &::after {
    content: '';
    width: 100%;
    height: 0.5px;
    background-color: var(--main-color);
    transform: translateY(-50%);
    top: 50%;
  }

  &::before {
    right: 0;
  }

  &::after {
    left: 0;
  }
}

.authServicesWrapper {
  display: flex;
  justify-content: space-around;
}

.linkAuthServises {
  width: 80px;
  height: 54px;
  border-radius: 10px;
  background-color: var(--contrasting-color);
  box-shadow: 2px 4px 5.8px 0px rgba(2, 18, 32, 0.06);
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  cursor: pointer;
}

.quesAuthWrapper {
  margin-top: 26px;

  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.question {
  margin-left: 20px;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.redirectLink {
  margin-right: 20px;
  color: var(--primary-blue-color);
  text-align: right;
  font-family: var(--main-font);
  font-weight: 600;
  text-transform: capitalize;

  &:hover {
    text-decoration: underline;
  }
}
