.choiseUnit {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 40px;
}

.title {
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 12px;
}

.secondTitle {
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.input {
  width: 320px;
  font-family: var(--main-font);
  font-size: 15px;
  text-transform: capitalize;
  padding: 12px 10px;
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 32px;
}

.img {
  width: 115px;
  height: 115px;
}

.text {
  text-align: center;
  text-transform: uppercase;
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 13px;
}

.btn {
  display: flex;
  cursor: pointer;
  border: none;
  background-color: transparent;
  padding: 10px;
  flex-direction: column;
  width: 115px;
}

.buttonList {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(5, 1fr);
  gap: 20px;
}
