.tableWrapper {
  overflow-y: auto;
  padding-right: 20px;

  &::-webkit-scrollbar {
    position: absolute;
    z-index: 12;
    width: 0.7em;
    border-radius: 7px;
    background: linear-gradient(
      90deg,
      rgba(204, 204, 204) 0%,
      rgba(204, 204, 204, 0) 50%,
      rgb(204, 204, 204) 100%
    );
  }

  &::-webkit-scrollbar-thumb {
    background: #005aaa;
    border-radius: 7px;
  }
}

.table {
  width: 100%;
}

.firstColumn {
  min-width: 400px;
}
.firstColumnDesc {
  display: flex;
  flex-direction: row;
  padding-top: 50px;
}

.firstColumnBtn {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

.secondColumn {
  border-right: 1px solid #000;
  border-left: 1px solid #000;
  min-width: 200px;
}
.secondColumnBtn {
  border-right: 1px solid #000;
  border-left: 1px solid #000;
}
.secondColumnDesc {
  border-right: 1px solid #000;
  border-left: 1px solid #000;
  padding-top: 50px;
}

.thirdColumn {
  min-width: 250px;
}
.thirdColumnBtn {
  text-align: end;
}
.thirdColumnDesc {
  text-align: end;
  padding-top: 50px;
}

.head {
  padding-top: 15px;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--main-color);
  border-top: 1px solid var(--main-color);
  color: var(--main-color);
  font-family: var(--main-font);
  font-size: 20px;
  font-weight: 500;
  line-height: 100%;
  text-transform: capitalize;
  text-align: center;
}

.label {
  display: flex;
  align-items: center;
  margin-right: 14px;
  height: 23px;
  font-family: var(--secondary-font);
  font-size: 20px;
  cursor: pointer;
  user-select: none;
}

.checkbox {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;

  &:checked + .customize::before {
    display: block;
  }
}

.customize {
  display: inline-block;
  width: 23px;
  height: 23px;
  border-radius: 3px;
  border: 1px solid black;

  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;

    background-image: url(../../images/svg/check.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    transform: translate(-50%, -50%);
    display: none;
  }
}

.createNewUnit {
  width: 250px;
  height: 29px;
  border-radius: 7px;
  background-color: var(--primary-blue-color);
  color: var(--contrasting-color);
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  display: block;
  margin: 20px 20px 0 0;
  cursor: pointer;
  border: none;
}

.price {
  margin: 20px 0 13px 7px;
  text-align: right;
  font-family: var(--secondary-font);
  font-size: 24px;
  text-transform: uppercase;
}

.nameWrapper {
  display: flex;
  align-items: center;
  margin-bottom: 9px;
}

.nameInput {
  font-family: var(--main-font);
  font-size: 18px;
  border-radius: 5px;
  padding: 3px;
  width: 120px;
}

.discountInput {
  font-family: var(--main-font);
  font-size: 16px;
  border-radius: 5px;
  padding: 4px 4px 2px 4px;
  width: 50px;
}

.priceInput::-webkit-outer-spin-button,
.priceInput::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.priceWrapper {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
}

.discountWrapper {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
}

.editBtn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cancelBtn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 7px;
}

.acceptBtn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-right: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.priceTitle {
  text-align: right;
  font-family: var(--main-font);
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 10px;
}

.priceInfo {
  margin-left: 6px;
  text-align: right;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.reviewBtn {
  padding: 5px 12px;
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 7px;
  background-color: var(--light-blue-color);
  height: 29px;
  border: none;
  cursor: pointer;
  margin-left: auto;
  margin-right: 20px;
}

.unitInfoWrapper {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
}

.img {
  width: 82px;
  height: 82px;
  border-radius: 10px;
  border: 1px solid var(--main-color);
}

.infoTitle {
  font-family: var(--main-font);
  font-size: 20px;
  text-decoration-line: underline;
  margin-right: 7px;
}

.priceInput {
  margin-top: 18px;
  margin-bottom: 13px;
  font-family: var(--main-font);
  font-size: 16px;
  border-radius: 5px;
  width: 130px;
  padding: 5px 5px 2px 5px;
}

.inputPriceWrapper {
  position: relative;
}

