.wrongPrice {
  color: var(--accent-red-color);
}

.changedPrice {
  color: var(--primary-blue-color);
}

.optionBtn {
  font-size: 16px;
  border: none;
  text-transform: uppercase;
  padding: 5px 19px;
  background-color: var(--light-blue-color);
  font-family: var(--secondary-font);
  cursor: pointer;
  border-radius: 7px;
}

.info {
  margin-bottom: 5px;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.dealerPrice {
  &:hover {
    color: var(--primary-blue-color);
    cursor: pointer;
  }
}
