.checkbox {
  text-transform: uppercase;
}

.color {
  color: #000;
  font-family: TT Norms;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  &:not(:last-child) {
    margin-bottom: 23px;
  }

  & svg {
    margin-right: 6px;
  }
}

.styleType {
  color: #000;
  font-family: TT Norms;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  & svg {
    margin-right: 6px;
  }
}

.styleTypeWrapper {
  margin-top: 56px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.wrapper {
  display: flex;
  justify-content: center;
}

.colorWrapper {
  width: 357px;
}

.formGroup {
  display: flex;
  &:not(:last-child) {
    margin-right: 20px;
    margin-bottom: 25px;
  }
}

.inputWrapper {
  display: flex;
  flex-direction: column;
}
