.wrappper {
  padding: 25px;
  background-color: var(--light-blue-color);
  border-radius: 10px;
  display: flex;
  justify-content: center;
}

.form {
  width: 340px;
}

.formGroup {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.error {
  font-size: 12px;
  margin: 5px 9px;
  color: var(--accent-red-color);
}

.btn {
  margin-top: 21px;
  padding: 5px 21px;
  color: var(--contrasting-color);
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  background-color: var(--primary-blue-color);
  border-radius: 7px;
  border: none;
  display: block;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
}

.formTitle {
  color: var(--text-color);
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  text-transform: uppercase;
}

.selectWrapper {
  position: relative;
}

.rotated {
  transform: rotate(180deg);
}

.arrow {
  transition: transform 0.5s ease;
}

.openBtn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  top: 32px;
  right: 10px;
}
