.tableWrapper {
  overflow-y: auto;
  padding-right: 20px;

  &::-webkit-scrollbar {
    position: absolute;
    z-index: 12;
    width: 0.7em;
    border-radius: 7px;
    background: linear-gradient(
      90deg,
      rgba(204, 204, 204) 0%,
      rgba(204, 204, 204, 0) 50%,
      rgb(204, 204, 204) 100%
    );
  }

  &::-webkit-scrollbar-thumb {
    background: var(--primary-blue-color);
    border-radius: 7px;
  }
}

.table {
  width: 100%;
}

.firstColumn {
  border-right: 1px solid var(--main-color);
  min-width: 400px;
}

.firstColumnDesc {
  display: flex;
  flex-direction: row;
}

.secondColumn {
  border-right: 1px solid var(--main-color);
  min-width: 200px;
}

.secondColumnDesc {
  border-right: 1px solid var(--main-color);
  border-left: 1px solid var(--main-color);
  vertical-align: baseline;
  text-align: center;
}

.thirdColumnDesc {
  vertical-align: baseline;
  text-align: end;
}

.head {
  padding-top: 15px;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--main-color);
  border-top: 1px solid var(--main-color);
  font-family: var(--main-font);
  font-size: 20px;
  font-weight: 500;
  line-height: 100%; /* 20px */
  text-transform: capitalize;
  text-align: center;
}

.orderInfoWrapper {
  width: 300px;
}

.title {
  font-family: var(--main-font);
  font-size: 20px;
  font-weight: 500;
  text-decoration: underline;
  text-transform: capitalize;
  margin-top: 30px;
  margin-bottom: 9px;
}

.subTitle {
  font-family: var(--main-font);
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 9px;
}

.info {
  font-family: var(--main-font);
  text-transform: capitalize;
}

.orderBtn {
  margin-right: 30px;
  margin-top: 30px;
  padding: 5px 12px;
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 7px;
  background-color: var(--light-blue-color);
  height: 29px;
  border: none;
  cursor: pointer;
}

.label {
  display: flex;
  align-items: center;
  margin-top: 28px;
  margin-right: 14px;
  height: 23px;
  font-family: var(--secondary-font);
  font-size: 20px;
  cursor: pointer;
  user-select: none;
}

.checkbox {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;

  &:checked + .customize::before {
    display: block;
  }
}

.customize {
  display: inline-block;
  width: 23px;
  height: 23px;
  border-radius: 3px;
  border: 1px solid var(--main-color);

  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;

    background-image: url(../../images/svg/check.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    transform: translate(-50%, -50%);
    display: none;
  }
}

.statusInfo {
  margin: 30px 0 0 0;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.price {
  margin-top: 30px;
  text-align: right;
  font-family: var(--secondary-font);
  font-size: 24px;
  text-transform: uppercase;
  margin-bottom: 13px;
}

.priceTitle {
  text-align: right;
  font-family: var(--main-font);
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 4px;
}

.priceInfo {
  margin-left: 6px;
  text-align: right;
  font-family: var(--main-font);
  text-transform: capitalize;
}

.spinnerWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wrongPrice {
  color: var(--accent-red-color);
}

.changedPrice {
  color: var(--primary-blue-color);
}

.updatePoBtn {
  font-size: 16px;
  border: none;
  text-transform: uppercase;
  padding: 5px 19px;
  background-color: var(--light-blue-color);
  font-family: var(--secondary-font);
  cursor: pointer;
  border-radius: 7px;
}
