.sideBar {
  min-width: 303px;
  padding: 14px;
  height: calc(93% + 67px);
  box-sizing: border-box;
  border-radius: 17px;
  background-color: var(--light-blue-color);
  display: flex;
  flex-direction: column;
}

.logo {
  width: 100%;
  margin-bottom: 14px;
}

.wrapper {
  padding: 13px;
  height: 88%;
  display: flex;
  flex-direction: column;
}

.name {
  color: var(--text-color);
  font-family: var(--main-font);
  font-size: 16px;
  font-weight: 600;
  text-transform: capitalize;
  margin-bottom: 13px;
}

.accountLink {
  color: var(--text-color);
  font-family: var(--main-font);
  text-decoration-line: underline;
  text-transform: lowercase;
  margin-bottom: 56px;
}

.userInfo {
  display: flex;
}

.userWrapper {
  display: flex;
  flex-direction: column;
}

.btn {
  width: 100%;
  height: 38px;
  border-radius: 7px;
  background-color: var(--primary-blue-color);
  color: var(--contrasting-color);
  cursor: pointer;
  border: none;
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  margin-left: auto;
  margin-right: auto;
  outline: none;
}

.link {
  display: block;
  text-decoration: line-through;
  text-decoration-line: underline;
  text-transform: capitalize;

  &:not(:last-child) {
    margin-bottom: 13px;
  }
}

.linkGroup {
  margin-top: auto;
}
