@import url('https://fonts.cdnfonts.com/css/spartan');
@import url('./window-calculator/shared/fonts/TTNorms/stylesheet.css');
@import url('./reset.scss');

:root {
  --primary-blue-color: #005aaa;
  --light-blue-color: #e5eef6;
  --light-gray-color: #d9d9d9;
  --accent-red-color: rgb(255, 5, 18);
  --secondary-hovered: rgb(239, 239, 239);
  --primary-hovered: #187ad0;
  --primary-disabled: rgb(209, 209, 209);
  --gray-color: rgb(142, 142, 142);
  --text-color: rgb(0, 0, 0);
  --main-color: rgb(0, 0, 0);
  --contrasting-color: rgb(255, 255, 255);
  --interactive-color: rgba(0, 90, 170, 0.1);
  --duration: 400ms;

  --main-font: Spartan;
  --secondary-font: TT Norms;
}

body {
  min-height: 100vh;
  margin: 0;
  font-family:
    var(--main-font),
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    'Spartan',
    'TT Norms',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  color: var(--text-color);
}

html,
body,
.app {
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

a {
  font-family: inherit;
  font-weight: inherit;
}
