.fieldGroup {
  display: flex;
  flex-direction: column;
}

.label {
  font-family: var(--main-font);
  text-transform: lowercase;
  margin: 13px 0 5px 9px;
}

.field {
  width: 100%;
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  font-family: var(--main-font);
  outline: none;
  padding: 13px 9px 13px 9px;
  box-sizing: border-box;
}

.error {
  font-size: 12px;
  margin: 5px 9px;
  color: var(--accent-red-color);
}

.btn {
  margin-top: 31px;
  padding: 14px 0 10px 0;
  width: 100%;
  border-radius: 10px;
  background-color: var(--primary-blue-color);
  color: var(--contrasting-color);
  outline: none;
  border: none;
  text-align: center;
  font-family: var(--main-font);
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  cursor: pointer;

  &:hover {
    background-color: #2f87d4;
  }

  transition: background-color 400ms;
}

.fieldWrapper {
  position: relative;
}

.showPasswordBtn {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 27px;
  height: 27px;
  right: 20px;
  transform: translateY(-50%);
  top: 50%;
  cursor: pointer;

  .eye * {
    transition: fill 400ms;
  }

  &:hover > .eye * {
    fill: gray;
  }

  .eye:global(.active) line {
    stroke: transparent;
  }
}

.labelCheck {
  font-family: var(--main-font);
  font-weight: 600;
  text-transform: capitalize;
  cursor: pointer;
  margin-top: 8px;
}

.extraArea {
  margin-top: 23px;
  display: flex;
}

.checkFieldGroup {
  width: 250px;
  display: flex;
}

.secondBtnGroup {
  margin-top: 7px;
  text-align: right;
}

.checkbox {
  margin-top: 3px;
  margin-right: 13px;
  width: 23px;
  height: 23px;
  cursor: pointer;
}

.fogotPassword {
  color: var(--primary-blue-color);
  text-align: start;
  font-family: var(--main-font);
  font-weight: 600;
  text-transform: capitalize;
  margin-left: auto;
  background-color: transparent;
  cursor: pointer;
  border: none;
  outline: none;

  &:first-child {
    margin-bottom: 8px;
  }

  &:hover {
    text-decoration: underline;
  }
}

.spinnerWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
