@import '../../window-calculator/shared/styles/media_mixin';

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.head {
  font-weight: 600;
  margin: 0 0 25px 9px;
  text-transform: uppercase;
}

.headAddress {
  margin-top: 25px;
  @include _1920 {
    margin-top: 0;
  }
}

.userForm {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
}

.fieldGroup {
  display: flex;
  flex-direction: column;
}

.thirdField::-webkit-outer-spin-button,
.thirdField::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.discountWrapper {
  position: relative;
  box-sizing: border-box;
  color: var(--accent-red-color);
  font-weight: 600;
  font-size: 20px;
}

.label {
  color: var(--text-color);
  text-transform: lowercase;
  margin: 13px 0 5px 9px;
}

.field {
  width: 340px;
  border-radius: 10px;
  border: 0.5px solid var(--text-color);
  color: var(--text-color);
  font-family: Spartan;
  outline: none;
  padding: 13px 9px 13px 9px;
}

.secondField {
  width: 160px;
  border-radius: 10px;
  border: 0.5px solid var(--text-color);
  color: var(--text-color);
  outline: none;
  padding: 13px 9px 13px 9px;
}

.secondFieldGroup {
  display: flex;
  flex-direction: column;
}

.edit {
  width: 107px;
  height: 110px;
  border-radius: 10px;
  background-color: var(--primary-blue-color);
  cursor: pointer;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.firstForm {
  margin-right: 88px;
}

.secondForm {
  display: flex;
  flex-direction: column;
  margin-right: 88px;
}

.firstWrapper {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}

.secondWrapper {
  display: flex;
  flex-direction: column;
}

.formWrapper {
  overflow-y: auto;
  display: flex;
  padding-right: 20px;

  &::-webkit-scrollbar {
    position: absolute;
    z-index: 12;
    width: 0.7em;
    border-radius: 7px;
    background: linear-gradient(
      90deg,
      rgba(204, 204, 204) 0%,
      rgba(204, 204, 204, 0) 50%,
      rgb(204, 204, 204) 100%
    );
  }

  &::-webkit-scrollbar-thumb {
    background: var(--primary-blue-color);
    border-radius: 7px;
  }
}

.firstSecondWrapper {
  @include _1920 {
    display: flex;
    padding-right: 20px;
  }
}

.rotated {
  transform: rotate(180deg);
}

.arrow {
  transition: transform 0.5s ease;
}

.error {
  font-weight: 400;
  font-size: 12px;
  margin: 9px 9px;
  color: var(--accent-red-color);
}

.customLabelCheckbox {
  display: flex;
  align-items: center;
  gap: 15px;
  color: var(--text-color);
  font-size: 16px;
  text-transform: lowercase;
  user-select: none;
  margin-bottom: 8px;
}

.checkbox {
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;

  &:checked + .customize::before {
    display: block;
  }
}

.customize {
  display: inline-block;
  width: 23px;
  height: 23px;
  border-radius: 3px;
  border: 1px solid var(--text-color);

  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    width: 16px;
    height: 16px;

    background-image: url(../../images/svg/check.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    transform: translate(-50%, -50%);
    display: none;
  }
}

.userDetails {
  margin-bottom: 17px;
}

.userDetailsForm {
  margin-bottom: 20px;
}

.statusName {
  color: var(--text-color);
  margin: 13px 0 13px 0;
  text-transform: lowercase;
}

.status {
  color: var(--accent-red-color);
  font-size: 20px;
  font-weight: 600;
  line-height: normal;
  text-transform: lowercase;
}

.customLabelForm {
  color: var(--text-color);
  text-transform: lowercase;
  margin: 0 0 8px 0;
}

.radioBtnHead {
  color: var(--text-color);
  text-transform: lowercase;
  margin: 0 0 8px 0;
}

.thirdField {
  width: 60px;
  color: var(--text-color);
  font-size: 20px;
  box-sizing: border-box;
  border-radius: 10px;
  border: 0.5px solid var(--main-color);
  padding: 9px 9px 7px 9px;
  margin-right: 9px;
}

.thirdFieldGroup {
  display: flex;
  flex-direction: column;
}

.btn {
  padding: 5px 22px;
  color: var(--contrasting-color);
  background-color: var(--primary-blue-color);
  text-align: center;
  font-family: var(--secondary-font);
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  display: inline-block;
  margin-top: 20px;
}

.btnWrapper {
  position: relative;
  display: flex;
  margin-top: auto;
  justify-content: center;
}

.logoutBtn {
  position: absolute;
  right: 25px;
  bottom: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-weight: 600;
  line-height: 15.68px;
  text-align: right;
  color: var(--primary-blue-color);
}

.spinnerWrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.openBtn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: absolute;
  top: 32px;
  right: 10px;
}

.selectWrapper {
  position: relative;
}
