.changeViewBtn {
  color: var(--primary-blue-color);
  text-align: center;
  font-family: var(--main-font);
  text-transform: uppercase;
  padding: 9px 20px 6px 20px;
  border-radius: 10px;
  background-color: var(--light-blue-color);
  border: none;
  cursor: pointer;
  display: block;
  margin-right: 10px;
}

.active {
  border: 1px solid var(--primary-blue-color);
}

.btnWrapper {
  display: flex;
}

.submitBtn {
  margin-top: 24px;
  border-radius: 7px;
  padding: 5px 22px;
  color: var(--contrasting-color);
  background-color: var(--primary-blue-color);
  text-transform: uppercase;
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
  border: none;
  cursor: pointer;
  font-family: var(--secondary-font);
  font-size: 16px;
}

.viewBtnWrapper {
  display: flex;
}

.changeViewWrapper {
  position: relative;
  margin-left: auto;
}

.passwordModal {
  position: absolute;
  top: 60px;
  left: 0;
  width: 385px;
  border-radius: 10px;
  border: 3px solid var(--accent-red-color);
  padding: 25px;
  box-sizing: border-box;
  z-index: 100;
  background-color: var(--contrasting-color);
}

.fieldWrapper {
  position: relative;
  margin-top: 11px;
}

.showPasswordBtn {
  position: absolute;
  background-color: transparent;
  border: none;
  width: 27px;
  height: 27px;
  right: 20px;
  transform: translateY(-50%);
  top: 50%;
  cursor: pointer;
}

.label {
  font-family: Spartan;
}

.field {
  width: 100%;
  border-radius: 10px;
  border: 0.5px solid var(--text-color);
  font-family: Spartan;
  outline: none;
  padding: 13px 9px 13px 9px;
  box-sizing: border-box;
}

.error {
  font-size: 12px;
  margin: 5px 9px;
  color: var(--accent-red-color);
}
