.wrapper {
  display: flex;
  justify-content: space-evenly;
}

.img {
  border-radius: 100%;
}

.typeText {
  color: #000;
  text-align: center;
  font-family: TT Norms;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
  margin-bottom: 5px;
}

.firstType {
  margin-right: 9px;
  position: relative;
  width: 165px;
}

.btn {
  border: none;
  width: 165px;
  height: 165px;
  border-radius: 11px;
  padding: 0;
  cursor: pointer;
}

.secondType {
  position: relative;
  width: 185px;
}

.typeImg {
  width: 100%;
  height: 100%;
  border-radius: 11px;
}

.title {
  color: #000;
  font-family: Spartan;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  text-transform: uppercase;
  text-align: start;
  margin-bottom: 5px;
}

.infoTitle {
  font-family: Spartan;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-align: start;
  text-align: center;
  margin: 0;
}

.infoLits {
  padding-left: 0;
  margin: 10px 0 0 0;
}

.infoLits li {
  font-family: Spartan;
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  text-align: start;
  margin-bottom: 8px;
}

.checkbox {
  margin: 10px 22px 14px 0;
}
