.header {
  margin-top: 60px;
  margin-bottom: 43px;

  display: flex;
  flex-direction: column;
  gap: 34px;
}

.topLine {
  display: flex;
  justify-content: center;
  position: relative;
}

.cartBtn {
  position: absolute;
  background-color: transparent;
  border: none;
  cursor: pointer;
  top: -15px;
  right: 0;

  &:global(.fill) {
    &::after {
      position: absolute;
      content: "";
      width: 29px;
      height: 29px;
      border-radius: 100%;
      background-color: rgb(255, 6, 19);
      right: 11px;
      z-index: -1;
    }
  }

  &:disabled {
    cursor: default;

    & > svg * {
      fill: var(--light-gray-color);
    }
  }
}
