@import './shared/styles/media_mixin';

.wrapper {
  position: relative;
}

.container {
  margin: auto;
  display: flex;
  flex-direction: column;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(4px);
  display: none;
  justify-content: center;
  align-items: center;

  &:global(.visible) {
    display: flex;
  }
}

.title {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;

  font-size: 16px;
  font-family: TT Norms;
  text-transform: uppercase;
  margin-bottom: 50px;

  & span {
    font-size: 16px;
  }
}

.main {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  gap: 40px;

  @include _768 {
    flex-direction: row;
  }
}

.mainHint {
  margin-top: 80px;
}

.block {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.navbarWrapper {
  margin-top: 28px;
}

.calculatorWrapper {
  position: relative;
}

.priceWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: absolute;
  right: 42px;
  bottom: 0;
}

.priceText {
  font-size: 16px;
  font-family: TT Norms;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.price {
  font-size: 16px;
  font-family: TT Norms;
  text-transform: uppercase;
  margin-left: 30px;
}

.dealerPriceText,
.dealerPrice {
  font-size: 14px;
  font-family: TT Norms;
  text-transform: capitalize;
}

.dealerPrice {
  margin-left: 35px;
}

.modal {
  z-index: 100;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
