@mixin button {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);

  border: 1px solid var(--main-color);
  background-color: transparent;
  cursor: pointer;
  border-radius: 100%;

  transition: background-color var(--duration);

  &:hover {
    background-color: var(--secondary-hovered);
  }
}

@mixin button-pseudo {
  content: '';
  width: 60%;
  height: 1px;
  background-color: var(--main-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.numberWrapper {
  position: relative;
  text-align: center;
  max-width: 160px;
  margin-left: auto;
  margin-right: auto;
}

.btnPlus {
  margin-right: 20px;
  @include button;
  right: 0;

  &::after,
  &::before {
    @include button-pseudo;
  }

  &::after {
    transform: translate(-50%, -50%) rotate(90deg);
  }
}

.btnMinus {
  margin-left: 20px;
  @include button;
  left: 0;

  &::before {
    @include button-pseudo;
  }
}

.number {
  font-family: TT Norms;
  color: var(--main-color);
}
