.title {
  display: block;
  width: 100%;
  font-size: 21px;
  font-weight: 700;
  text-align: center;
}

.wrapper {
  display: flex;
  justify-content: center;
  gap: 44px;
}

.buttonWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 19px;

  & p {
    max-width: 157px;

    font-size: 14px;
    font-family: TT Norms;
    text-transform: uppercase;
    text-align: center;
    line-height: normal;
  }
}
