.userWrapper {
  display: flex;
  height: 100%;
}

.secondWrapper {
  margin-left: 24px;
  width: 100%;
  height: 100%;
}

.link {
  color: var(--primary-blue-color);
  font-family: var(--secondary-font);
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 10px;
  border: 1px solid var(--primary-blue-color);
  background-color: white;
  cursor: pointer;
  display: inline-block;
  padding: 13px 33px;

  &:global(.active) {
    background-color: var(--light-blue-color);
    border-color: var(--light-blue-color);
    color: var(--primary-blue-color);
  }

  &:not(:last-of-type) {
    margin-right: 42px;
  }
}

.navWrapper {
  display: flex;
  margin-left: 40px;
  align-items: baseline;
}

.viewBtn {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-left: auto;
}

.userInfo {
  position: relative;
  height: 93%;
  margin-top: 20px;
  box-shadow: 2px 0px 8px 0px #8e8e8e3b;
  border-radius: 17px;
}

.outletWrapper {
  height: 100%;
  padding: 25px;
  box-sizing: border-box;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
